* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 50%, #f093fb 100%);
  background-size: 400% 400%;
  animation: gradientShift 15s ease infinite;
  min-height: 100vh;
  overflow-x: hidden;
}

@keyframes gradientShift {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

.app {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  position: relative;
}

.app::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="grain" width="100" height="100" patternUnits="userSpaceOnUse"><circle cx="25" cy="25" r="1" fill="rgba(255,255,255,0.1)"/><circle cx="75" cy="75" r="1" fill="rgba(255,255,255,0.1)"/><circle cx="50" cy="10" r="0.5" fill="rgba(255,255,255,0.05)"/><circle cx="10" cy="60" r="0.5" fill="rgba(255,255,255,0.05)"/><circle cx="90" cy="40" r="0.5" fill="rgba(255,255,255,0.05)"/></pattern></defs><rect width="100" height="100" fill="url(%23grain)"/></svg>');
  opacity: 0.3;
  pointer-events: none;
  animation: float 20s ease-in-out infinite;
}

@keyframes float {
  0%, 100% { transform: translateY(0px) rotate(0deg); }
  50% { transform: translateY(-20px) rotate(1deg); }
}

.container {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(20px);
  border-radius: 30px;
  box-shadow: 
    0 25px 50px rgba(0, 0, 0, 0.15),
    0 0 0 1px rgba(255, 255, 255, 0.2),
    inset 0 1px 0 rgba(255, 255, 255, 0.3);
  padding: 50px;
  width: 100%;
  max-width: 650px;
  min-height: 600px;
  position: relative;
  animation: slideInUp 0.8s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

@keyframes slideInUp {
  from {
    opacity: 0;
    transform: translateY(50px) scale(0.9);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

h1 {
  text-align: center;
  color: #333;
  margin-bottom: 40px;
  font-size: 3rem;
  font-weight: 700;
  background: linear-gradient(135deg, #667eea, #764ba2, #f093fb);
  background-size: 200% 200%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  animation: gradientText 3s ease infinite;
  text-shadow: 0 0 30px rgba(102, 126, 234, 0.3);
  position: relative;
}

@keyframes gradientText {
  0%, 100% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
}

h1::after {
  content: '‚ú®';
  position: absolute;
  top: -10px;
  right: -20px;
  font-size: 1.5rem;
  animation: sparkle 2s ease-in-out infinite;
}

@keyframes sparkle {
  0%, 100% { transform: scale(1) rotate(0deg); opacity: 1; }
  50% { transform: scale(1.2) rotate(180deg); opacity: 0.8; }
}

.input-section {
  display: flex;
  gap: 15px;
  margin-bottom: 40px;
  animation: slideInDown 0.6s ease-out 0.2s both;
}

@keyframes slideInDown {
  from {
    opacity: 0;
    transform: translateY(-30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.task-input {
  flex: 1;
  padding: 20px 25px;
  border: 2px solid transparent;
  border-radius: 50px;
  font-size: 16px;
  outline: none;
  transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  background: linear-gradient(white, white) padding-box,
              linear-gradient(135deg, #667eea, #764ba2) border-box;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  position: relative;
}

.task-input:focus {
  transform: translateY(-2px);
  box-shadow: 
    0 8px 25px rgba(102, 126, 234, 0.2),
    0 0 0 4px rgba(102, 126, 234, 0.1);
  border-color: transparent;
}

.task-input::placeholder {
  color: #999;
  transition: all 0.3s ease;
}

.task-input:focus::placeholder {
  transform: translateY(-2px);
  opacity: 0.7;
}

.add-button {
  padding: 20px 35px;
  background: linear-gradient(135deg, #667eea, #764ba2, #f093fb);
  background-size: 200% 200%;
  color: white;
  border: none;
  border-radius: 50px;
  font-size: 16px;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  white-space: nowrap;
  position: relative;
  overflow: hidden;
  animation: gradientShift 3s ease infinite;
}

.add-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  transition: left 0.6s ease;
}

.add-button:hover::before {
  left: 100%;
}

.add-button:hover {
  transform: translateY(-3px) scale(1.05);
  box-shadow: 
    0 15px 35px rgba(102, 126, 234, 0.4),
    0 0 0 1px rgba(255, 255, 255, 0.2);
}

.add-button:active {
  transform: translateY(-1px) scale(1.02);
  transition: all 0.1s ease;
}

.add-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

.add-button.adding {
  animation: pulse 1s ease-in-out infinite;
}

@keyframes pulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.05); }
}

.tasks-section {
  margin-bottom: 30px;
  animation: slideInUp 0.8s ease-out 0.4s both;
}

.no-tasks {
  text-align: center;
  color: #999;
  font-style: italic;
  padding: 60px 0;
  font-size: 20px;
  animation: pulse 2s ease-in-out infinite;
  position: relative;
}

.no-tasks::before {
  content: 'üìù';
  display: block;
  font-size: 3rem;
  margin-bottom: 20px;
  animation: bounce 2s ease-in-out infinite;
}

@keyframes pulse {
  0%, 100% { opacity: 0.7; }
  50% { opacity: 1; }
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% { transform: translateY(0); }
  40% { transform: translateY(-10px); }
  60% { transform: translateY(-5px); }
}

.task-list {
  list-style: none;
  animation: fadeInUp 0.6s ease-out;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.task-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 25px;
  margin-bottom: 15px;
  background: linear-gradient(135deg, #f8f9fa 0%, #ffffff 100%);
  border-radius: 20px;
  transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  border-left: 5px solid transparent;
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.08);
  animation: slideInRight 0.5s ease-out;
}

@keyframes slideInRight {
  from {
    opacity: 0;
    transform: translateX(50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.task-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(102, 126, 234, 0.1), transparent);
  transition: left 0.6s ease;
}

.task-item:hover::before {
  left: 100%;
}

.task-item:hover {
  background: linear-gradient(135deg, #e3f2fd 0%, #f8f9fa 100%);
  transform: translateX(8px) translateY(-2px);
  box-shadow: 
    0 8px 25px rgba(102, 126, 234, 0.15),
    0 0 0 1px rgba(102, 126, 234, 0.1);
  border-left-color: #667eea;
}

.task-item.completed {
  background: linear-gradient(135deg, #d4edda 0%, #c3e6cb 100%);
  border-left-color: #28a745;
  opacity: 0.8;
  animation: completeTask 0.6s ease-out;
}

@keyframes completeTask {
  0% { transform: scale(1); }
  50% { transform: scale(1.05); }
  100% { transform: scale(1); }
}

.task-text {
  flex: 1;
  font-size: 16px;
  color: #333;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  padding: 8px 0;
  position: relative;
  font-weight: 500;
}

.task-item.completed .task-text {
  text-decoration: line-through;
  color: #6c757d;
  position: relative;
}

.task-item.completed .task-text::after {
  content: '‚úì';
  position: absolute;
  right: -30px;
  top: 50%;
  transform: translateY(-50%);
  color: #28a745;
  font-weight: bold;
  font-size: 18px;
  animation: checkmark 0.5s ease-out;
}

@keyframes checkmark {
  0% { transform: translateY(-50%) scale(0); }
  50% { transform: translateY(-50%) scale(1.2); }
  100% { transform: translateY(-50%) scale(1); }
}

.task-text:hover {
  color: #667eea;
  transform: translateX(5px);
}

.delete-button {
  padding: 10px 20px;
  background: linear-gradient(135deg, #dc3545, #c82333);
  color: white;
  border: none;
  border-radius: 25px;
  font-size: 14px;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  margin-left: 15px;
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 15px rgba(220, 53, 69, 0.3);
}

.delete-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  transition: left 0.4s ease;
}

.delete-button:hover::before {
  left: 100%;
}

.delete-button:hover {
  background: linear-gradient(135deg, #c82333, #bd2130);
  transform: translateY(-2px) scale(1.05);
  box-shadow: 
    0 8px 25px rgba(220, 53, 69, 0.4),
    0 0 0 1px rgba(255, 255, 255, 0.2);
}

.delete-button:active {
  transform: translateY(0) scale(0.98);
  transition: all 0.1s ease;
}

.delete-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

.task-item.deleting {
  animation: slideOutRight 0.3s ease-in-out forwards;
  opacity: 0;
}

@keyframes slideOutRight {
  from {
    opacity: 1;
    transform: translateX(0) scale(1);
  }
  to {
    opacity: 0;
    transform: translateX(100px) scale(0.8);
  }
}

.stats {
  text-align: center;
  padding: 25px;
  background: linear-gradient(135deg, #f8f9fa 0%, #e9ecef 100%);
  border-radius: 20px;
  margin-top: 30px;
  animation: slideInUp 0.8s ease-out 0.6s both;
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.stats::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(102, 126, 234, 0.1), transparent);
  animation: shimmer 3s ease-in-out infinite;
}

@keyframes shimmer {
  0% { left: -100%; }
  50% { left: 100%; }
  100% { left: 100%; }
}

.stats p {
  color: #666;
  font-size: 16px;
  font-weight: 600;
  position: relative;
  z-index: 1;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
}

/* Loading animation for new tasks */
@keyframes taskSlideIn {
  from {
    opacity: 0;
    transform: translateX(-50px) scale(0.9);
  }
  to {
    opacity: 1;
    transform: translateX(0) scale(1);
  }
}

.task-item.new-task {
  animation: taskSlideIn 0.5s ease-out;
}

/* Pulse animation for empty state */
@keyframes emptyStatePulse {
  0%, 100% { 
    transform: scale(1);
    opacity: 0.8;
  }
  50% { 
    transform: scale(1.02);
    opacity: 1;
  }
}

.no-tasks {
  animation: emptyStatePulse 3s ease-in-out infinite;
}

/* Enhanced focus states */
.task-input:focus,
.add-button:focus,
.delete-button:focus {
  outline: none;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.3);
}

/* Smooth transitions for all interactive elements */
* {
  transition: box-shadow 0.3s ease;
}

/* Custom scrollbar for task list */
.task-list {
  max-height: 400px;
  overflow-y: auto;
  padding-right: 5px;
}

.task-list::-webkit-scrollbar {
  width: 6px;
}

.task-list::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.1);
  border-radius: 3px;
}

.task-list::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, #667eea, #764ba2);
  border-radius: 3px;
}

.task-list::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(135deg, #5a6fd8, #6a4190);
}

/* Responsive design */
@media (max-width: 768px) {
  .container {
    padding: 30px 20px;
    margin: 10px;
    border-radius: 25px;
  }
  
  h1 {
    font-size: 2.5rem;
  }
  
  h1::after {
    right: -15px;
    top: -5px;
  }
  
  .input-section {
    flex-direction: column;
    gap: 15px;
  }
  
  .task-input {
    padding: 18px 22px;
  }
  
  .add-button {
    padding: 18px 30px;
    align-self: stretch;
  }
  
  .task-item {
    flex-direction: column;
    align-items: flex-start;
    gap: 15px;
    padding: 20px;
  }
  
  .delete-button {
    margin-left: 0;
    align-self: flex-end;
    padding: 12px 24px;
  }
  
  .task-item.completed .task-text::after {
    right: -25px;
  }
}

@media (max-width: 480px) {
  .container {
    padding: 20px 15px;
    margin: 5px;
  }
  
  h1 {
    font-size: 2rem;
  }
  
  .task-input {
    padding: 15px 18px;
    font-size: 14px;
  }
  
  .add-button {
    padding: 15px 25px;
    font-size: 14px;
  }
  
  .task-text {
    font-size: 14px;
  }
  
  .delete-button {
    padding: 10px 20px;
    font-size: 12px;
  }
}
